<template>
  <div class="showView">
    <BlogItem v-for="item in listArr" :key="item.id" :myObj="item" />
    <div class="pageGroup">
      <div class="btn btnLeft" @click="btnLeft">&lt;上一页</div>
      <div class="btn btnRight" @click="btnRight">下一页&gt;</div>
    </div>
    <div class="mask" v-show="showMask">数据加载中...</div>
  </div>
</template>

<script>
import BlogItem from '@/components/BlogItem'
export default {
  data() {
    return {
      listArr: [],
      page: 1,
      showMask: false
    }
  },
  components: {
    BlogItem
  },
  created() {
    this.getData()
  },
  methods: {
    // 点击上一页按钮
    btnLeft() {
      if (this.page <= 1) {
        alert('已经是第一页了')
        return
      }
      this.page--
      this.getData()
      this.scrollTop()
      this.showMask = true
    },
    // 点击下一页按钮
    btnRight() {
      if (this.listArr.length < 9 || this.listArr.length == 0) {
        alert('已经是最后一页了')
        return
      }
      this.page++
      this.getData()
      this.scrollTop()
      this.showMask = true
    },
    // 返回顶部的函数
    scrollTop() {
      if (document.body.scrollTop) {
        document.body.scrollTop = 0
      } else {
        document.documentElement.scrollTop = 0
      }
    },
    getData() {
      this.axios({
        url: 'https://ku.qingnian8.com/dataApi/blog/showBlog.php',
        params: { page: this.page }
      }).then((res) => {
        console.log(res)
        this.listArr = res.data
        this.showMask = false
      })
    }
  }
}
</script>

<style lang="less" scoped>
.mask {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  background: rgba(0, 0, 0, 0.4);
  color: orange;
  font-size: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.showView {
  padding: 30px;
}
.pageGroup {
  padding: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  .btn {
    border: 1px solid #41b883;
    padding: 10px 20px;
    font-size: 18px;
    color: #41b883;
    margin: 0 10px;
    cursor: pointer;
    transition: 0.3s;
    &:hover {
      background: #41b883;
      color: #fff;
    }
  }
}
</style>
